.staysInLos {
  position: relative;
  letter-spacing: 0.02em;
  line-height: 48px;
  display: inline-block;
  width: 1280px;
}
.options,
.title {
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.options {
  display: flex;
  flex-direction: row;
}
.title {
  width: 1440px;
  padding: var(--padding-0) var(--padding-61xl);
  box-sizing: border-box;
}
.banner,
.header,
.title {
  display: flex;
  flex-direction: column;
}
.banner {
  width: 1440px;
  height: 192.6px;
  padding: 45.60009765625px var(--padding-0);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-end;
  background-image: url(/public/banner@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.header {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
}
.formTitle,
.formTitleSubtext {
  align-self: stretch;
  position: relative;
}
.formTitleSubtext {
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.formTitle {
  font-size: var(--font-size-11xl);
  color: var(--color-darkslategray-100);
}
.formHeader {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.fillInYour {
  align-self: stretch;
  position: relative;
  line-height: 34px;
}
.input {
  border: 0;
  background-color: transparent;
  align-self: stretch;
}
.formFields {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.button,
.form,
.formFields {
  display: flex;
  flex-direction: column;
}
.button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-orange-200);
  border-radius: var(--br-9xs);
  width: 164px;
  height: 56px;
  overflow: hidden;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
}
.button:hover {
  background-color: var(--color-darkorange);
}
.form {
  align-self: stretch;
  border-radius: var(--br-xs);
  background-color: var(--background-paper);
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.03);
  border: 1px solid var(--color-whitesmoke-200);
  padding: var(--padding-9xl) var(--padding-11xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
  text-align: left;
  color: var(--color-slategray);
}
.newsletterFormSection,
.subscribeForm {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
}
.subscribeForm {
  width: 549px;
  padding: var(--padding-xl);
  justify-content: flex-start;
  gap: var(--gap-11xl);
  margin-left: auto;
  margin-right: auto;
  position: relative !important;
  left: auto !important;
}
.newsletterFormSection {
  width: 1440px;
  height: 529px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-23xl) var(--padding-0);
  justify-content: flex-end;
  background-image: url(/public/newsletter-form-section@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.logoIcon {
  position: relative;
  width: 174px;
  height: 33px;
  overflow: hidden;
  flex-shrink: 0;
}
.fickleFlightIs {
  align-self: stretch;
  position: relative;
  line-height: 27px;
}
.socialIcons {
  position: relative;
  width: 130px;
  height: 30px;
}
.fickleflightBio {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-sm);
  margin-bottom: 40px !important;
}
.seperator {
  position: relative;
  border-right: 1px solid var(--color-gray-300);
  box-sizing: border-box;
  width: 1px;
  height: 157.87px;
}
.company1 {
  position: relative;
  line-height: 27px;
  font-weight: 500;
  display: inline-block;
  width: 174.72px;
}
.aboutUs,
.careers,
.howWeWork,
.news {
  position: relative;
  display: inline-block;
  width: 135.67px;
}
.careers,
.howWeWork,
.news {
  width: 84.28px;
}
.careers,
.howWeWork {
  width: 115.11px;
}
.howWeWork {
  width: 197.33px;
}
.company,
.frame {
  width: 197.33px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame {
  height: 112px;
  overflow: hidden;
  flex-shrink: 0;
  gap: var(--gap-xs);
  font-size: var(--typography-body-1-size);
}
.company {
  height: 159.76px;
  gap: var(--gap-xl);
}
.support1 {
  position: relative;
  line-height: 27px;
  font-weight: 500;
  display: inline-block;
  width: 131.56px;
}
.accessibility,
.account,
.faq,
.more1 {
  position: relative;
  display: inline-block;
  width: 109.63px;
}
.accessibility,
.faq,
.more1 {
  width: 52.99px;
}
.accessibility,
.more1 {
  width: 164.44px;
}
.more1 {
  line-height: 27px;
  font-weight: 500;
  width: 76.65px;
}
.airlineFees,
.covidAdvisory,
.tips {
  position: relative;
  display: inline-block;
  width: 172.87px;
}
.airlineFees,
.tips {
  width: 135.36px;
}
.tips {
  width: 50.56px;
}
.footerLinks,
.more {
  display: flex;
  align-items: flex-start;
}
.more {
  width: 197.33px;
  height: 159.76px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-xl);
}
.footerLinks {
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  font-size: var(--font-size-xl);
}
.footer,
.footerSection,
.hotelsPage {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.footer {
  align-self: stretch;
  background: linear-gradient(91.74deg, #0b5ba8 25.38%, #299bd8);
  flex-direction: row;
  padding: var(--padding-14xl) var(--padding-65xl);
  gap: var(--gap-25xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--background-paper);
  font-family: var(--typography-body-1);
}
.footerSection,
.hotelsPage {
  flex-direction: column;
}
.footerSection {
  align-self: stretch;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
  font-family: var(--typography-body-1);
}
.hotelsPage {
  position: relative;
  background-color: var(--background-paper);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-23xl);
  color: var(--background-paper);
  font-family: var(--font-baloo-bhai);
}
@media screen and (max-width: 1200px) {
  .footer,
  .title {
    box-sizing: border-box;
  }
  .title {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
  }
  .footer {
    padding: var(--padding-21xl);
  }
}
@media screen and (max-width: 960px) {
  .title {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
  .subscribeForm {
    padding: var(--padding-5xl) var(--padding-0);
    box-sizing: border-box;
  }
  .fickleflightBio {
    flex: unset;
    align-self: stretch;
  }
  .seperator {
    display: none;
  }
  .footerLinks {
    flex: unset;
    align-self: stretch;
  }
  .footer {
    flex-direction: column;
    padding: var(--padding-21xl) var(--padding-5xl) var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .staysInLos {
    font-size: var(--font-size-13xl);
  }
  .button {
    width: 100%;
  }
  .button:hover {
    background-color: var(--color-orange-200);
  }
  .subscribeForm {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
  .company {
    margin-bottom: 40px !important;
  }
  .footerLinks {
    flex-direction: column;
  }
  .footer {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
