.results {
  position: relative;
  letter-spacing: 0.26px;
  display: inline-block;
  width: 65.26px;
}
.frame,
.stops {
  width: 296.62px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame {
  height: 56px;
  overflow: hidden;
  flex-shrink: 0;
  gap: var(--gap-base);
}
.stops {
  height: 106px;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--font-size-3xl);
  color: var(--color-dimgray-300);
  font-family: var(--typography-body-1);
}
