.mostPopularDestinations,
.planYourNext {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.mostPopularDestinations {
  font-size: var(--font-size-11xl);
  text-transform: capitalize;
  color: var(--color-darkslategray-300);
}
.titleContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
}
.viewAllDestinations {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: var(--typography-body-1);
  color: var(--color-cornflowerblue-200);
  text-align: right;
  display: inline-block;
  width: 197.33px;
  flex-shrink: 0;
}
.arrowRightIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.destinationsTitles,
.viewAllTop {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.viewAllTop {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--background-paper);
  border-radius: var(--br-12xl);
  width: 231.38px;
  align-items: flex-start;
  gap: var(--gap-3xs);
}
.destinationsTitles {
  align-self: stretch;
  align-items: center;
}
.paris {
  flex: 1;
  position: relative;
  letter-spacing: 0.02em;
}
.div,
.from {
  position: relative;
  display: inline-block;
}
.from {
  letter-spacing: 0.04em;
  text-transform: uppercase;
  width: 52.53px;
}
.div {
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
  width: 90.92px;
}
.destinationDetails,
.details {
  display: flex;
  justify-content: flex-start;
}
.details {
  width: 90.92px;
  height: 59px;
  flex-direction: column;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  box-sizing: border-box;
  align-items: flex-end;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--typography-body-1-size);
}
.destinationDetails {
  align-self: stretch;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-xs);
  align-items: flex-start;
}
.pariscard,
.pariscardWrapper {
  height: 182px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.pariscard {
  align-self: stretch;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-0) 0.29296875px;
  align-items: flex-start;
  justify-content: flex-start;
  background-image: url(/public/pariscard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.pariscardWrapper {
  flex: 1;
  padding: var(--padding-0) var(--padding-3xs);
  align-items: center;
  justify-content: space-between;
  margin-top: 20;
}
.div1 {
  position: relative;
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
}
.details1,
.greececard,
.norwaycard {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: flex-start;
}
.details1 {
  width: 99.92px;
  height: 59px;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  align-items: flex-end;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--typography-body-1-size);
}
.greececard,
.norwaycard {
  align-self: stretch;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-0) 0.29296875px;
  align-items: flex-start;
  background-image: url(/public/greececard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.norwaycard {
  background-image: url(/public/norwaycard@3x.png);
}
.div3 {
  position: relative;
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
  display: inline-block;
  width: 100.62px;
}
.details3,
.tuscanycard {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: flex-start;
}
.details3 {
  width: 100.62px;
  height: 59px;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  align-items: flex-end;
  gap: var(--gap-11xs);
  text-align: right;
  font-size: var(--typography-body-1-size);
}
.tuscanycard {
  align-self: stretch;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-0) 0.29296875px;
  align-items: flex-start;
  background-image: url(/public/tuscanycard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.cardsContainer,
.tuscanycardWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tuscanycardWrapper {
  flex: 1;
  height: 182px;
  flex-direction: column;
  padding: var(--padding-0) var(--padding-3xs);
  box-sizing: border-box;
  margin-top: 20;
}
.cardsContainer {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  column-gap: 20;
  font-size: var(--font-size-xl);
  color: var(--background-paper);
}
.viewAllDestinations1 {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 500;
  display: inline-block;
  width: 197.33px;
  flex-shrink: 0;
}
.popDestinationsMain,
.viewAllBottom {
  align-items: flex-start;
  justify-content: flex-start;
}
.viewAllBottom {
  border-radius: var(--br-12xl);
  background-color: var(--background-paper);
  width: 231.38px;
  display: none;
  flex-direction: row;
  gap: var(--gap-3xs);
  text-align: right;
}
.popDestinationsMain {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  gap: var(--gap-5xl);
  width: 100%;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
  font-family: var(--typography-body-1);
}
@media screen and (max-width: 960px) {
  .viewAllTop {
    display: none;
  }
  .cardsContainer {
    flex-wrap: wrap;
  }
  .viewAllDestinations1 {
    width: auto;
  }
  .viewAllBottom {
    display: flex;
    margin-top: 16px;
  }
}
@media screen and (max-width: 420px) {
  .mostPopularDestinations {
    font-size: var(--font-size-3xl);
  }
  .titleContainer {
    flex: unset;
    align-self: stretch;
  }
  .destinationsTitles {
    flex-direction: column;
    align-self: flex-start;
  }
}
